.about-top {
  .about-top-menu {
    height: 4.1vw;
    background-color: #00b8b8;
    padding-left: 3.1vw;
    ul {
      list-style: none;
      display: flex;
      align-items: center;
      height: 100%;
      li {
        display: block;
        margin-right: 30px;
        a {
          color: #fff;
          font-size: calc(11px + 5 * (100vw - 320px) / 447);
          height: calc(65px + 15 * (100vw - 320px) / 447);
          line-height: calc(35px + 11 * (100vw - 320px) / 447);
          margin-right: calc(10px + 5 * (100vw - 320px) / 447);
        }
      }
    }
  }
  .about-top-image {
    position: relative;
    height: 22vw;
    background-image: url(../img/about/title-bg.jpg);
    background-size: cover;
    img {
      width: 100%;
    }
    .top-visual-title {
      position: absolute;
      top: 50%;
      text-align: center;
      color: #fff;
      width: 100%;
      letter-spacing: 0.24em;
      font-size: 2.8vw;
      z-index: 1;
      font-family: fot-tsukuardgothic-std, sans-serif;
      font-weight: 700;
      font-style: bold;
    }
  }
  .about-top-image:after {
    content: "";
    background-color: rgba(0, 0, 0, 0.13);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  @media screen and (max-width: 767px) {
    .about-top-menu {
      height: auto;
      ul {
        display: block;
        li {
          display: block;
          margin-right: 30px;
          a {
            color: #fff;
          }
        }
      }
    }
    .about-top-image {
      position: relative;
      height: 30vh;
      img {
        height: 100%;
        width: auto;
      }
      .top-visual-title {
        position: absolute;
        top: 50%;
        width: 100%;
        letter-spacing: 0.24em;
        font-size: 5vw;
        z-index: 1;
      }
    }
  }
}
