.course-middle {
	h2 {
		font-size: 2.8vw;
		line-height: 3.4vw;
		border-bottom: 3px solid #2B3B77;
		width: fit-content;
		text-align: center;
		margin: 0 auto 2vw;
	}
	h3 {
		font-size: 2.3vw;
		line-height: 3.4vw;
	}
	h4 {
		line-height: 3.1vw;
		font-size: 2vw;
	}
	p {
		font-size: 1.1vw;
		line-height: 2.6vw;
		letter-spacing: .15em;
	}
	.sp-br {
		display: none;
		@media screen and (max-width: 760px) {
			display: block;
		}
	}
	.middle-outer-red,
	.middle-outer-green,
	.middle-outer-blue {
		h2 {
			color: #2B3B77;
			text-align: center;
			letter-spacing: .24em;
		}
	}
	.middle-outer-red {
		margin-top: 8vw;
	}
	.middle-red {
		padding-top: 3vw;
		padding-bottom: 3vw;
		background-image: url(../img/course/red-bg.png);
		background-repeat: no-repeat;
		height: 611px;
		.middle-inner-red {
			width: 82%;
			margin: 0 auto;
			display: flex;
			.red-inner {
				width: 584px;
				margin-right: auto;
				h3 {
					margin-bottom: 1.2vw;
					font-size: 2.5vw;
					letter-spacing: 0.24em;
				}
				h4 {
					letter-spacing: 0.24em;
				}
				h5 {
					margin-bottom: 1.6vw;
					font-size: 1.5vw;
					letter-spacing: 0.24em;
				}
			}
			.red-inner-right {
				width: 45%;
				img {
					width: 100%;
				}
				.btn-green {
					width: 18vw;
					margin-left: auto;
					margin-top: 3.5vw;
					transition: all .3s;
					-webkit-transition: all .3s; 
			
					&:hover{
						opacity: 0.8; 
						filter: brightness(110%); 
					}
					img {
						width: 100%;
					}
				}
			}
		}
	}
	.middle-outer-blue {
		margin-top: 8vw;
	}
	.middle-blue {
		padding-top: 3vw;
		padding-bottom: 3vw;
		background-image: url(../img/course/blue-bg.png);
		background-repeat: no-repeat;
		height: 611px;
		.middle-inner-blue {
			width: 82%;
			margin: 0 auto;
			display: flex;
			.blue-inner {
				width: 584px;
				margin-right: auto;
				h3 {
					margin-bottom: 1.2vw;
					font-size: 2.5vw;
					letter-spacing: 0.24em;
				}
				h4 {
					letter-spacing: 0.24em;
				}
				h5 {
					margin-bottom: 1.6vw;
					font-size: 1.5vw;
					letter-spacing: 0.24em;
					line-height:1.5;
				}
			}
			.blue-inner-right {
				width: 45%;
				img {
					width: 100%;
				}
				.btn-green {
					width: 18vw;
					margin-left: auto;
					margin-top: 3.5vw;
					transition: all .3s;
					-webkit-transition: all .3s; 
			
					&:hover{
						opacity: 0.8; 
						filter: brightness(110%); 
					}
					img {
						width: 100%;
					}
				}
			}
		}
	}
	.middle-outer-green {
		margin-top: 8vw;
	}
	.middle-green {
		padding-top: 4.5vw;
		height: 611px;
		padding-bottom: 3vw;
		background-image: url(../img/course/green-bg.png);
		background-repeat: no-repeat;
		.middle-inner-green {
			width: 82%;
			margin: 0 auto;
			display: flex;
			.green-inner {
				width: 584px;
				margin-right: auto;
				h3 {
					margin-bottom: 2.5vw;
					letter-spacing: 0.24em;
				}
				h4 {
					margin-bottom: 3vw;
					letter-spacing: 0.13em;
				}
			}
			.green-inner-right {
				width: 45%;
				img {
					width: 100%;
				}
				.btn-green {
					width: 18vw;
					margin-left: auto;
					margin-top: 3.5vw;
					transition: all .3s;
					-webkit-transition: all .3s; 
			
					&:hover{
						opacity: 0.8; 
						filter: brightness(110%); 
					}
					img {
						width: 100%;
					}
				}
			}
		}
	}
	@media screen and (max-width: 1480px) {
		.middle-green, .middle-red, .middle-blue {
			height: auto;
		}
	}
	@media screen and (max-width: 767px) {
		h2 {
			font-size: 5vw;
			line-height:1.7;
			border-bottom: 3px solid #2B3B77;
			width: fit-content;
			text-align: center;
			margin-bottom:calc(15px + 5 * (100vw - 320px) / 447);
		}
		h3 {
			font-size: 4.5vw;
			line-height:1.7;
		}
		h4 {
			line-height:1.7;
			font-size: 4vw;
		}
		p {
			font-size: 2.4vw;
			line-height:1.7;
			letter-spacing: .36em;
		}
		.btn-green {
			width: 55% !important;
			margin: 0 auto;
			img {
				width: 100%;
			}
		}
		.middle-outer-red,
		.middle-outer-green,
		.middle-outer-blue {
			height: auto;
			h2 {
				color: #2B3B77;
				text-align: center;
			}
		}
		.middle-outer-red {
			margin-top: 6vw;
		}
		.middle-red {
			padding-top: 4.5vw;
			padding-bottom: 3vw;
			background-repeat: no-repeat;
			.middle-inner-red {
				width: 90%;
				margin: 0 auto;
				display: block;
				.red-inner {
					width: 90%;
					margin: 0 auto 3vw;
					h3 {
						font-size: 4.5vw;
						line-height:1.5;
						margin-bottom: 1.2vw;
					}
					h4 {
						margin-top: 2.1vw;
						margin-bottom: 2.4vw;
					}
				}
				.red-inner-right {
					width: 70%;
					margin: 0 auto;
					img {
						width: 100%;
					}
				}
			}
		}
		.middle-outer-blue {
			margin-top: 8vw;
		}
		.middle-blue {
			padding-top: 4.5vw;
			padding-bottom: 3vw;
			background-repeat: no-repeat;
			.middle-inner-blue {
				width: 90%;
				margin: 0 auto;
				display: block;
				.blue-inner {
					width: 90%;
					margin: 0 auto 3vw;
					h3 {
						font-size: 4.5vw;
						line-height:1.5;
						margin-bottom: 1.2vw;
					}
					h5 {
						margin-bottom: 1.6vw;
						font-size: 3vw;
					}
				}
				.blue-inner-right {
					width: 70%;
					margin: 0 auto;
					img {
						width: 100%;
					}
				}
			}
		}
		.middle-outer-green {
			margin-top: 8vw;
		}
		.middle-green {
			padding-top: 4.5vw;
			padding-bottom: 3vw;
			background-repeat: no-repeat;
			.middle-inner-green {
				width: 90%;
				margin: 0 auto 3vw;
				display: block;
				.green-inner {
					width: 90%;
					margin: 0 auto 3vw;
				}
				.green-inner-right {
					width: 70%;
					margin: 0 auto;
					img {
						width: 100%;
					}
				}
			}
		}
	}
	@media screen and (max-width: 599px) {
		p {
			font-size: 3.3vw;
			letter-spacing:0.36em;
		}
	}
}